<template>
  <v-container fluid>
    <v-data-iterator
      :items="items"
      :items-per-page.sync="itemsPerPage"
      :page.sync="page"
      :search="this.settings.search"
      :sort-by="this.settings.sortBy"
      :sort-desc="this.settings.sortDesc"
      hide-default-footer
    >
      <template v-slot:default="props">
        <v-row>
          <v-col
            v-for="item in props.items"
            :key="item.name"
            cols="12"
            sm="6"
            md="4"
            lg="3"
          >
            <iteratorcard
              :key="item.name"
              v-bind:items="props.items"
              v-bind:config="config"
              v-bind:item="item"
              v-bind:filteredKeys="filteredKeys"
            />
          </v-col>
        </v-row>
      </template>
      <template v-slot:footer>
        <v-row class="mt-2 no-margin" align="center" justify="center">
          <span class="white--text">Items per page</span>
          <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                dark
                text
                color="white"
                class="ml-2 blue-background"
                v-bind="attrs"
                v-on="on"
              >
                {{ itemsPerPage }}
                <v-icon>mdi-chevron-down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="(number, index) in [8, 12, 16, 20]"
                :key="index"
                @click="updateItemsPerPage(number)"
              >
                <v-list-item-title>{{ number }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>

          <v-spacer></v-spacer>

          <span class="mr-4 white--text">
            Page {{ page }} of {{ numberOfPages }}
          </span>
          <v-btn dark color="blue darken-3" class="mr-1" @click="formerPage">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-btn dark color="blue darken-3" class="ml-1" @click="nextPage">
            <v-icon>mdi-chevron-right</v-icon>
          </v-btn>
        </v-row>
      </template>
    </v-data-iterator>
  </v-container>
</template>

<script>
import iteratorcard from "./iteratorcard.vue";
const parseJSON = async (self) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      let pvs = [];

      fetch("config.json")
        .then((response) => response.json())
        .then((data) => {
          self.headers = data.headers;
          for (const item of data.items) {
            self.config.push(item.config);
            self.items.push(item.fields);
            pvs = pvs.concat(item.config.pvs);
          }
          resolve(pvs);
        });
    }, 100);
  });
};

export default {
  props: ["settings"],
  components: {
    iteratorcard,
  },
  data() {
    return {
      filter: {},
      page: 1,
      itemsPerPage: 8,
      headers: [],
      items: [],
      config: [],
      symbols: { temperature: " C", pressure: " hPa", voltage: " V" },
    };
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.settings.keys.filter((key) => key !== "Name");
    },
  },
  methods: {
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
  },
  created() {
    let self = this;
    let ws = new WebSocket("ws://127.0.0.1:5678/");

    ws.onopen = async function () {
      let pvs = await parseJSON(self);

      ws.send(pvs);
    };

    ws.onmessage = function (e) {
      const pv = JSON.parse(e.data);
      const type = pv["name"].toLowerCase();
      const index = self.config.findIndex((i) => i.pvs.includes(type));

      switch (type) {
        case "pressure":
          self.items[index]["rack open"] =
            pv.value > self.config[index].hatch ? "No" : "Yes";
          break;
        default:
          break;
      }

      self.items[index][type] = pv.value.toFixed(2) + self.symbols[type];
    };
  },
};
</script>

<style scoped>
.no-margin {
  margin: 0;
}

.blue-background {
  background: rgb(21, 101, 192);
}
</style>